.home-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.625rem;
    background: #464444;
    height: 100vh;
    text-transform: capitalize;
}

.mode-bar {
    display: flex;
    padding: 2.25rem 6.25rem;
    justify-content: space-between;
    align-items: center;
    align-self: stretch;
}

.mode-button {
    display: flex;
    padding: 0.625rem 1.875rem;
    flex-direction: column;
    align-items: flex-start;
    gap: 0.625rem;
    border-style: none;

    color: #000;
    font-family: Inter;
    font-size: 2.5rem;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    border-radius: 1.75rem;
    text-transform: capitalize;
}

.title-container {
    display: flex;
    padding: 1.5625rem 0.625rem;
    justify-content: center;
    align-items: center;
    gap: 0.625rem;
    align-self: stretch;

    color: #FFF;
    font-family: Inter;
    font-size: 4rem;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
}

.game-container {
    display: flex;
    padding: 5.0625rem 17.1875rem;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    flex: 1 0 0;
    align-self: stretch;

    color: #FFF;
    font-family: Inter;
    font-size: 2.5rem;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}

.prompt {
    display: flex;
    padding: 0.625rem 0rem;
    justify-content: space-between;
    align-items: center;
    align-self: stretch;
}

.flash-result {
    display: none;
    padding: 0.625rem;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 0.625rem;
    align-self: stretch;
}

.stats-container {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 0.625rem;
    align-self: stretch;
}

.form {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1.875rem;
    align-self: stretch;
}

.number-field {
    width: 6.25rem;
    height: 6.25rem;
    display: flex;
    align-items: center;

    font-size: 4rem;
}

.submit-button {
    display: flex;
    padding: 1.25rem 0rem;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 0.625rem;
    align-self: stretch;
    border-style: none;

    color: #000;
    font-family: Inter;
    font-size: 2.5rem;
    font-style: normal;
    font-weight: 400;
    line-height: normal;

    border-radius: 2.875rem;
}

@media (max-width: 768px) {
    .game-container {
        justify-content: center;
        gap: 20rem;
    }
    .prompt {
        font-size: 5rem;
    }
    .submit-button {
        font-size: 4rem;
    }
    .title-container {
        font-size: 7rem;
        height: 50rem;
    }
}