.login-page {
    display: flex;
    padding: 0.625rem;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 6.6875rem;
    background-color: #B93A3A;
    height: 100vh;
}

.image-container {
    display: flex;
    width: 70.75rem;
    height: 16.125rem;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 0.625rem;
    flex-shrink: 0;
}

.image {
    object-fit: contain;
}

.login-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1.1875rem;
    align-self: stretch;
    padding: 0rem 40rem;
}

.field {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 1.1875rem;
    align-self: stretch;

    color: #1E1E1E;
    font-family: var(--sds-typography-body-font-family);
    font-size: 2.25rem;
    font-style: normal;
    font-weight: var(--sds-typography-body-font-weight-regular);
    line-height: 100%;
    /* 2.25rem */
}

.login-button {
    display: flex;
    justify-content: center;
    align-items: center;
    align-self: stretch;

    border-radius: var(--Radius-200, 0.5rem);
    border: var(--sds-size-stroke-border) solid #2C2C2C;
    background: var(--Background-Brand-Default, #2C2C2C);
}

.input-field {
    display: flex;
    min-width: 15rem;
    align-items: center;
    align-self: stretch;
}

@media (max-width: 768px) {
    .login-page {
        display: flex;
        height: 100vh;
        padding: 0.625rem 0rem;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 0.625rem;
    }

    .image-container {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        overflow: hidden;
        width: 100%;
        height: 40%;
    }

    .image {
        object-fit: contain;
        width: 100%;
        height: 100%;
    }

    .login-container {
        display: flex;
        padding: 0rem 5.6875rem;
        flex-direction: column;
        align-items: center;
        gap: 3.1875rem;
        align-self: stretch;
    }

    .field-container {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        gap: 1.1875rem;
        align-self: stretch;

        color: #1E1E1E;
        font-family: Inter;
        font-size: 2.25rem;
        font-style: normal;
        font-weight: 400;
        line-height: 140%;
        /* 3.15rem */
    }


    .login-button {
        display: flex;
        justify-content: center;
        align-items: center;
        align-self: stretch;
    }

    .input-field {
        display: flex;
        min-width: 15rem;
        padding: var(--sds-size-space-300) var(--sds-size-space-400);
        align-items: center;
        align-self: stretch;
    }

    .field {
        font-size: 5rem;
    }
}